#notifications-wrapper {
  left: 0;
  position: fixed;
  z-index: 101;
  width: 100%;
  pointer-events: none;
}

#notifications {
  text-align: center;
}

#notifications .notification {
  display: inline-block;
  padding: 5px 10px;
  border-radius: 0 0 3px 3px;
  border-width: 0 1px 1px;
  color: #fff !important;
  pointer-events: auto;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-weight: 600;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
}

#notifications .notification.notice {
  background: #5286d9;
}


.spinner { display: inline-block; vertical-align: bottom; width: 24px; height:24px; background: url(../images/spinner.gif) no-repeat 50% 50%; }
.spinner.big { width: 48px; height: 48px; background: url(../images/spinner_big.gif) no-repeat 50% 50%; }

@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (-moz-min-device-pixel-ratio: 1.5), only screen and (-o-min-device-pixel-ratio: 3 / 2), only screen and (min-device-pixel-ratio: 1.5), only screen and (min-resolution: 1.5dppx) {
  .spinner { background-image: url(../images/spinner_2x.gif); background-size: 20px; }
  .spinner.big { background-image: url(../images/spinner_big_2x.gif); background-size: 48px; }
}